.home{
    /* border: 1px solid red; */
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-around;
    
}

.home>div:nth-child(1){
    width: 30%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid red; */

}
.home>div:nth-child(2){
  
    width: 70%;
    font-weight: bold;
    text-align: left;
    padding: 10% 10%;
    /* border: 1px solid blue; */
}
.home>div:nth-child(1)>img{
   border-radius: 5px;
    width: 60%;
    box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}
.home>div:nth-child(2)>p{
    font-weight: 400;
}
h1{
    color: 
    #fa437c;
}
.info>h3{
    color: 
    #fa437c; 
}
.info>a>button{
    padding: 5px 10px;
    border: 1px solid ;
    border-radius: 5px;
    background-color: white;
    color: #fa437c;
}
.info>a>button:hover{
    background-color: #fa437c;
    color: white;
    border: 1px solid white;
}
@media All and (max-width: 480px){
    .home{
        /* border: 1px solid red; */
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 30px;
        
    }
  
    .home>div:nth-child(2){
        text-align: center;
  
        
    }
}
@media All and (min-width: 481px) and (max-width: 768px){
    .home{
        /* border: 1px solid red; */
        width: 100%;
        height: auto;
        display: flex;
        justify-content: space-around;

        
    }
   
    .home>div:nth-child(1){
        /* border: 1px solid red; */
        width: 50%;

        
    }
    .home>div:nth-child(2){
        text-align: left;
        width: 50%;
        
    }
}