.project{
    text-align: center;
    margin-top: 100px;
}
.project_div{
    width: 90%;
    margin: auto;
    /* border: 2px solid #fa437c; */
    /* padding: 10px 20px; */
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}
.project_div:hover{
 border: 2px solid #fa437c;
}
.project_div>div:nth-child(1){
    width: 60%;
    /* border: 1px solid red; */
}
.project_div>div:nth-child(2){
    width: 40%;
    padding: 10px;
    text-align: left;
    margin-left: 20px;
    /* border: 1px solid red; */
}
.project_div>div>img{
    width: 100%;
}
.proinfo>h4{
    color:#fa437c ;
}

@media All and (max-width: 480px){
    .project{
        text-align: center;
        margin-top: 100px;
    }
}
