.contact{
    text-align: center;
    background-color: #ecf0f3;
    margin-top: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
}
.contact>div{
    /* border: 1px solid black; */
    padding-top: 10px;
    padding-bottom: 10px;
    width: 40%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
   
    
}
.contact_div>div{
    box-shadow: rgba(0, 0, 0, 0.05) 0px 0px 0px 1px;
    padding: 7px 14px;
    background-color: white;
    text-decoration: none;
    margin-left: 1%;
}
.contact_div>div>a{
    text-decoration: none;
    color: black;
    /* margin-left: 1%; */
}
.contact_div>div:hover{
   background-color: #fa437c;
   color: white;
}
