.skill{
    text-align: center;
    margin-top: 20px;
    height: auto;
    /* background-color: #ecf0f3; */
    /* border: 1px solid red; */
}

.skills_div{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
   
    gap: 5%;
    width: 70%;
    margin: auto;
}

.skills_div>div{
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
    padding: 5px 10px;
    width: auto;
    height: auto;
    border-radius: 5px;
}
.skills_div>div>img{
  width: 30%;
  margin-bottom: 10%;
  margin-top: 10%;
}
.skills_div>div:hover{
    background-color: #fa437c;
    color: white;
}

@media All and (max-width: 480px){
    .skills_div{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
  
        gap: 5%;
        width: 90%;
        margin: auto;
    }
    .skill{
        text-align: center;
        margin-top: 20px;
        margin-bottom: 50px;
        height: auto;
    }
}
@media All and (min-width: 481px) and (max-width: 768px){
    .skills_div{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 5%;
        width: 70%;
        margin: auto;
    }
    .skill{
        text-align: center;
        margin-top: 20px;
        margin-bottom: 20px;
        height: auto;
    }
}